import { useSnackbar } from 'notistack'
import { Helmet } from 'react-helmet-async'
import { useNavigate } from 'react-router'

import { useMediaQuery, useTheme } from '@mui/material'

import CLUB_CATEGORIES, { type ClubCategories } from 'src/data/categories'
import { CLUB_SEARCH_MESSAGE } from 'src/data/messages'

import Catchphrase from 'src/components/Catchphrase'
import Searchbox from 'src/components/Searchbox'

import RecruitingClubs from 'src/content/index/Index/RecruitingClubs'

const Index = () => {
  const theme = useTheme()
  const isMqUpperThanSm = useMediaQuery(theme.breakpoints.up('sm'))
  const navigate = useNavigate()
  const { enqueueSnackbar } = useSnackbar()

  const handleSubmit: React.FormEventHandler<HTMLFormElement> = event => {
    event.preventDefault()
    const search = document.getElementById('search') as HTMLInputElement
    navigate(`search?name=${search.value}`)
  }

  const handleClickCategory: React.MouseEventHandler<HTMLButtonElement> = event => {
    const category = event.currentTarget.getAttribute('data-value') as ClubCategories

    if (category !== null && !CLUB_CATEGORIES.includes(category)) {
      enqueueSnackbar(CLUB_SEARCH_MESSAGE.WRONG_CATEGORY, { variant: 'warning' })
      return
    }
    const search = document.getElementById('search') as HTMLInputElement
    navigate(`search?name=${search.value}&category=${category}`)
  }

  return (
    <>
      <Helmet>
        <title>KLUB :: All the Clubs on KLUB</title>
      </Helmet>
      <Catchphrase>
        <Searchbox searchDefaultValue={''} handleSubmit={handleSubmit} handleClickCategory={handleClickCategory} />
      </Catchphrase>
      <RecruitingClubs theme={theme} isMqMatched={isMqUpperThanSm} />
    </>
  )
}

export default Index
