import FavoriteIcon from '@mui/icons-material/Favorite'
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder'
import { IconButton } from '@mui/material'

interface WishBtnProps {
  handleWish: React.MouseEventHandler<HTMLButtonElement>
  isWish: boolean
}

const WishBtn: React.FC<WishBtnProps> = ({ handleWish, isWish = false }: WishBtnProps) => {
  return (
    <IconButton onClick={handleWish} size="small">
      {isWish ? (
        <FavoriteIcon fontSize="small" color="primary" />
      ) : (
        <FavoriteBorderIcon fontSize="small" color="secondary" />
      )}
    </IconButton>
  )
}

export default WishBtn
