import { useState } from 'react'

import { Box, Card, CardActionArea, CardContent, Chip, Stack, Typography, useMediaQuery, useTheme } from '@mui/material'

import { type ClubBelonging } from 'src/data/belonging'

import WishBtn from 'src/components/WishBtn'

import { stringToColor } from 'src/utils'

export interface ClubPreviewProps {
  id: number
  name: string
  category: string
  belonging: ClubBelonging
  description: string
}

function ClubPreview({ id, name, category, belonging, description }: ClubPreviewProps) {
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))

  const [isWish, setIsWish] = useState(false)
  const handleWish = (event: React.MouseEvent<HTMLButtonElement>) => {
    event.preventDefault()
    setIsWish(!isWish)
  }

  return (
    <Card>
      <CardActionArea href={`/club/${id}`} sx={{ display: 'flex', flexDirection: matches ? 'column' : 'row' }}>
        <Box sx={{ backgroundColor: stringToColor(name, 0.3), height: '100px', width: { sm: '100%', xs: '10%' } }} />
        <CardContent sx={{ flex: 1, width: '100%' }}>
          <Box display="flex" flexDirection="row" alignItems="center" justifyContent="space-between">
            <Typography gutterBottom variant="h5" component="div">
              {name}
            </Typography>
            <Box sx={{ marginTop: theme.spacing(-0.5) }}>
              <WishBtn handleWish={handleWish} isWish={isWish} />
            </Box>
          </Box>
          {matches && (
            <Typography
              variant="body2"
              color="text.secondary"
              whiteSpace="nowrap"
              overflow="hidden"
              textOverflow="ellipsis"
            >
              {description}
            </Typography>
          )}
          <Stack direction="row" justifyContent="flex-start" spacing={1} sx={{ marginTop: theme.spacing(1) }}>
            {belonging && (
              <Chip label={belonging} size="small" sx={{ padding: theme.spacing(0.5), fontSize: matches ? 13 : 11 }} />
            )}
            {category && (
              <Chip label={category} size="small" sx={{ padding: theme.spacing(0.5), fontSize: matches ? 13 : 11 }} />
            )}
          </Stack>
        </CardContent>
      </CardActionArea>
    </Card>
  )
}

export default ClubPreview
